import React from 'react';
import { css, jsx } from '@emotion/core'

export default function Cell1(props) {
    let { icon, title, subtitle } = props
    if (icon.length == 0) {
        icon = 'sign_car'
    }

    return (
        <div>
            <img css={css`
                height: 2.5em;
            `}src={require(`../../images/${icon}.svg`)} />
            <h2>{title}</h2>
            <h3>{subtitle}</h3>
        </div>
    )
}