import React from 'react';
import { css, jsx } from '@emotion/core'

export default function Navigation(props) {
    const app = props.app
    const headline = app.headline

    return (
        <nav css={css`
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: space-between;
            align-items: center;
            width: 65%;
            margin: 0 auto;
            padding-top: 2em;
        `}>
            <Logo app={app} />
            <span css={css`
                display: flex;
                align-items: center;
            `}>
                <Twitter app={app} />
                <span css={css`
                    a {
                        text-decoration: none;
                        font-size: 1.1em;
                        font-weight: 700;
                        padding: 0.3em 0.3em;
                    }
        
                    a:hover {
                        border-radius: 0.2em;
                        background-color: rgba(50, 50, 50, 0.1);
                        color: #3F3F3F;
                    }

                    a:first-child {
                        margin-right: 0.5em;
                    }
                `}>
                    <Features/>
                    <Download />
                </span> 
            </span>
        </nav>
    )
}

function Features(props) {
    return (
        <a
            href='#features'
            onClick={() => {
                const options = {
                    behavior: 'smooth'
                }
                document.getElementById('features-section').scrollIntoView(options)
            }}
        >
            Features
        </a>
    )
}

function Download(props) {
    return (
        <a
            href="#download"
            onClick={() => {
                const options = {
                    behavior: 'smooth'
                }
                document.getElementById('download-section').scrollIntoView(options)
            }}
        >
            Download
        </a>
    )
}

function Twitter(props) {
    const app = props.app
    if (app.header.twitter === undefined) {
        return (
            <div></div>
        )
    }

    return (
        <a css={css`
            margin-right: 0.6em;
            &:hover {
                filter: saturate(2);
            }
        `} href={`https://twitter.com/${app.header.twitter}`}>
            <img css={css`
                width: 1.5em;
                height: 1.5em;
            `}
                src={require('../images/twitter.png')}
            />
        </a>
    )
}

function Logo(props) {
    const app = props.app
    console.log('adasd', props)
    return (
        <div css={css`
            display: flex;
            justify-content: center;
            align-items: center;
        `}>
            <img css={css`
                width: 50px;
                height: auto;
            `}
                src={require(`../../../data/images/${app.slug}/icon.png`)}
            />
            <span css={css`
                font-size: 1.4em;
                margin-left: 10px;
                font-weight: bold;
                user-select: none;
            `}>
                {app.name}
            </span>
        </div>
    )
}