/** @jsx jsx */
import React from 'react';
import { css, jsx } from '@emotion/core'

import BlockS1 from './BlockS1'
import BlockI1 from './BlockI1'
import BlockH1 from './BlockH1'
import BlockH2 from './BlockH2'
import BlockG1 from './BlockG1'

export default function Blocks(props) {
    if (props.app.blocks === undefined) {
        return (<div></div>)
    }

    const blocks = props.app.blocks.map((obj) => {
        switch (obj.style) {
            case 'g1': return <BlockG1 {...obj} />
            case 's1': return <BlockS1 {...obj} />
            case 'i1': return <BlockI1 {...obj} />
            case 'h1': return <BlockH1 {...obj} />
            case 'h2': return <BlockH2 {...obj} />
            default: return <div />
        }
    })

    return (
        <div css={css`
            margin: 5em 0;
        `}>
            {blocks}
        </div>
    )
}