import React from 'react';
import { css, jsx } from '@emotion/core'
import AppstoreButtons from './buttons/AppstoreButtons'

function DownloadLinks(props) {
    const app = props.app
    const download = app.download

    let links = []

    links.push(
        <AppstoreButtons download={download} />
    )
    links.push(FreeTrial())

    return (
        <div css={css`
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;

            @media (max-width: 500px) {
                flex-direction: column;

                a {
                    margin-top: 20px;
                }
            }
        `}>
            {links}
        </div>
    )
}

