/** @jsx jsx */
import React from 'react';
import { css, jsx } from '@emotion/core'
import Img from 'react-image'
import Spinner from "react-spinners/RingLoader";
import resolveImage from './resolveImage'
import AppstoreButtons from './buttons/AppstoreButtons'

export default function Headline(props) {
    const app = props.app
    const headline = app.headline
    return (
        <div css={css`
            display: flex;
            flex-direction: column;
            align-items: center;
        `}>
            <p css={css`
                font-size: 4em;
                text-align: center;
                font-style: bold;
                font-weight: 800;
                width: 50%;
                white-space: pre-line;
                @media (max-width: 500px) {
                    width: 90%;
                    font-size: 2em;
                }
            `}>{headline.title}</p>
            <p css={css`
                font-style: italic;
                font-size: 1.3em;
                text-align: center;
                width: 50%;
                color: #2E2E2E;
                white-space: pre-line;
                margin-top: -1em;
                @media (max-width: 500px) {
                    width: 80%;
                    font-size: 1em;
                }
            `}>{headline.text}</p>
            <AppstoreButtons download={app.download} />
            <Img css={css`
                width: 98%;
                height: auto;
            `}
                src={resolveImage(app, headline)}
                alt={headline.title}
                loader={
                    <div css={css`
                        height: 500px;
                        display: grid;
                        align-items: center;
                    `}>
                        <Spinner
                            size={60}
                            color={"#2E2E2E"}
                            loading={true}
                        />
                    </div>
                }
            />
        </div>
    )
}