/** @jsx jsx */
import React from 'react';
import { css, jsx } from '@emotion/core'

import Cell2 from './Cell2'

export default function BlockH1(props) {
    const { title, imageUrl, items } = props
    const cells = items.map((item) => {
        return <Cell2 {...item} />
    })

    return (
        <div css={css`
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-column-gap: 2em;
            @media (max-width: 500px) {
                grid-template-columns: 1fr;
                &:nth-child(0) {
                    padding: 0 1em;
                }
            }
            margin: 2em 1em 0 1em;
        `}>
            <div css={css`
                display: grid;
                grid-row-gap: 0.5em;
                align-content: center;
                padding-left: 10em;
                @media (max-width: 500px) {
                    padding: 0 5em;
                }
            `}>
                <h2 css={css`
                    font-weight: 600;
                    font-size: 1.8em;
                `}>{title}</h2>
                {cells}
            </div>
            <img css={css`
                width: 100%;
            `}src={imageUrl} />
        </div>
    )
}