import React from 'react';
import { css, jsx } from '@emotion/core'

export default function Cell1(props) {
    let { icon, title, subtitle } = props
    if (icon.length == 0) {
        icon = 'sign_car'
    }

    return (
        <div css={css`
            display: flex;
        `}>
            <img css={css`
                width: 2.5em;
                object-fit: scale-down;
                margin-right: 1em;
                align-self: flex-start;
                padding-top: 1em;
            `} src={require(`../../images/${icon}.svg`)} />
            <div>
                <h2>{title}</h2>
                <h3>{subtitle}</h3>
            </div>
        </div>
    )
}