/** @jsx jsx */
import React from 'react';
import { css, jsx } from '@emotion/core'

export default function AppstoreButtons(props) {
    const { download } = props
    if (download.appstore === undefined) {
        return (<div />)
    }

    const buttons = download.appstore.map((platform) => {
        return <Button platform={platform} />
    })

    return (
        <div css={css`
            display: flex;
            flex-direction: row;
            align-items: center;
            a {
                text-decoration: none;
            }

            a:active {
                position: relative;
                top: 1px;
            }

            a:hover {
                transform: scale(1.1);
            }
        `}>
            {buttons}
        </div>
    )
}

function Button(props) {
    const { platform } = props
    let height = '50px'
    let paddingTop = '0px'
    let textMarginTop = '-5px'
    if (platform.platform === 'iOS') {
        height = '60px'
    }
    if (platform.platform === 'macOS') {
        paddingTop = '4px'
        textMarginTop = '-2px'
    }

    return (
        <div css={css`

        `} key={platform.id}>
            <a css={css`
                display: inline-block;
                overflow: hidden;
                width: 180px;
                height: ${height};
                padding-top: ${paddingTop}
            `}
                href={`https://apps.apple.com/app/${platform.id}`}
            >
                <img css={css`
                    width: 100%;
                    object-fit: scale-down;
                `}src={require(`../../images/download ${platform.platform}.svg`)} />
            </a>
            <p css={css`
                font-size: 0.8em;
                margin-top: ${textMarginTop};
                text-align: center;
            `}>
                {`Require ${platform.platform} ${platform.version}`}
            </p>
        </div>
    )
}