import React from 'react';
import { css, jsx } from '@emotion/core'
import Cell1 from './Cell1'

export default function BlockG1(props) {
    const { items } = props
    const cells = items.map((item) => {
        return <Cell1 {...item} />
    })
    
    return (
        <div css={css`
            display: grid;
            grid-template-columns: repeat(3, 0.2fr);
            margin: 0 auto;
            justify-content: center;
            grid-gap: 2em;
            margin: 5em 0;
        `}>
            {cells}
        </div>
    )
}